@keyframes mainScreenFadeIn {
  0% {
    transform: translateY(20px);
    @media only screen and (min-width: 960px) {
      transform: translateY(20px);
    }
    @media only screen and (min-width: 1280px) {
      transform: translateY(1.25vw);
    }
    @media only screen and (min-width: 1600px) {
      transform: translateY(20px);
    }
    opacity: 0;
  }
  100% {
    opacity: 1;
    transform: none;
  }
}
@keyframes mainScreenFocusInFadeIn {
  0% {
    transform: translateY(20px);
    @media only screen and (min-width: 960px) {
      transform: translateY(20px);
    }
    @media only screen and (min-width: 1280px) {
      transform: translateY(1.25vw);
    }
    @media only screen and (min-width: 1600px) {
      transform: translateY(20px);
    }
    filter: blur(12px);
    letter-spacing: 0.05em;
    opacity: 0;
  }
  100% {
    filter: blur(0);
    transform: none;
    opacity: 1;
  }
}
@keyframes mainScreenSlideLeftFadeIn {
  0% {
    transform: translateX(-50px);
    @media only screen and (min-width: 960px) {
      transform: translateX(-50px);
    }
    @media only screen and (min-width: 1280px) {
      transform: translateX(-3.125vw);
    }
    @media only screen and (min-width: 1600px) {
      transform: translateX(-50px);
    }
    filter: blur(12px);
    letter-spacing: 0.05em;
    opacity: 0;
  }
  100% {
    filter: blur(0);
    transform: none;
    opacity: 1;
  }
}
@keyframes mainScreenSlideLeftBottomFadeIn {
  0% {
    transform: translateX(-50px) translateY(50px);
    filter: blur(12px);
    letter-spacing: 0.05em;
    opacity: 0;
  }
  100% {
    transform: translateX(0) translateY(0);
    filter: blur(0);
    opacity: 1;
  }
}
@keyframes mainScreenScaleInFadeIn {
  0% {
    transform: translateY(-25px) scaleY(0.9) scaleX(0.9);
    filter: blur(12px);
    letter-spacing: 0.05em;
    opacity: 0;
  }
  100% {
    transform: translateY(0) scaleY(1) scaleX(1);
    filter: blur(0);
    opacity: 1;
  }
}
@keyframes mainScreenPuffInFadeIn {
  0% {
    transform: scaleX(1.2);
    filter: blur(12px);
    letter-spacing: 0.05em;
    opacity: 0;
  }
  100% {
    transform: scaleX(1);
    filter: blur(0);
    opacity: 1;
  }
}
